// import { swiper, swiperSlide } from "vue-awesome-swiper";

import PromotionGood from "@/components/PromotionGood";
import {
	getGroomList
} from "@/api/store";
export default {
	name: "GoodsPromotion",
	components: {
		// swiper,
		// swiperSlide,
		PromotionGood
	},
	props: {},
	data: function() {
		return {
			imgUrls: [],
			goodsList: [],
			RecommendSwiper: {
				pagination: {
					el: ".swiper-pagination",
					clickable: true
				},
				autoplay: {
					disableOnInteraction: false,
					delay: 2000
				},
				loop: true,
				speed: 1000,
				observer: true,
				observeParents: true
			}
		};
	},
	mounted: function() {
		this.getIndexGroomList();
	},
	methods: {
		getIndexGroomList: function() {
			let that = this;
			getGroomList(4)
				.then(res => {
					that.imgUrls = res.data.banner;
					that.goodsList = res.data.list;
				})
				.catch((err) => {
					uni.showToast({
						title: err.msg || err.response.data.msg,
						icon: 'none',
						duration: 2000
					});
				});
		}
	}
};
